html, body, #root {
  width: 100%;
  height: 100%;
}

.furigana {
  /* -webkit-transform: scale(0.75); */
  /* transform: scale(0.75) translateX(-5px); */
  /* -webkit-text-size-adjust: none;
  text-size-adjust: none; */
  font-size: 50%;
  text-align: center;
}

.surface {
  text-align: center;
}
.clickable:hover {
  background-color: rgba(162, 244, 255, 0.75);
  cursor: pointer;
}

.bookmark {
  border-bottom: 1px solid #000;
  font-weight: bold;
}

.paragraph {
  margin-top: 12px;
}

@media screen {
  .a4 {
    width: 21cm;
    height: 29.7cm;
    display: block;
    margin: 16px auto;
    box-shadow: 0 0 0.1cm rgba(0,0,0,0.5);
    border: 0px;
  }
  .a4 > div {
    padding: 0.5cm 1.1cm;
  }
}


@page {
  size: A4;
  margin: 0.5cm 0.8cm;
}
@media print {
  form {
    display: none;
  }
  .content {
    margin: 0px;
  }
  
}
